<template>
  <!--Find the name of the page, transition to it-->
  <router-view v-slot="{ Component }">
    <transition :name="transitionName">
      <component :is="Component" />
    </transition>
  </router-view>
  <div class="start" v-if="isHidden">
    <h2>Press</h2>
    <span><h1 v-on:click="startThePage" v-if="ola">start</h1></span>
    <h2>to begin!</h2>
  </div>
  <div class="when-start" v-if="!isHidden">
    <router-link v-if="disabled" v-bind:to="pageName[pageNumber]"
      ><div class="right-chev">
        <h1 v-if="delayshowRight">&#62;<a class = "click-here-text" v-bind:v-if="clickHere"> Click Here</a></h1>
      </div></router-link
    >
    <router-link v-if="disabled" v-bind:to="pageName2[pageNumber]"
      ><div class="left-chev">
        <h1 v-if="delayshowLeft">&#60;</h1>
      </div></router-link
    >
    <div>
      <img
        class="sprite-position"
        :src="require('../assets/' + images[currentNumber])"
      />
    </div>
    <div class="bottom-border"></div>
  </div>
</template>

<script>
import { reactive } from "@vue/reactivity";
export default {
  name: "images",
  data() {
    return {
      pageName: ["/", "/About", "/Australia", "/Nz", "/null"],
      pageName2: ["/null", "", "/", "/About", "/Australia"],
      delayshowLeft: true,
      delayshowRight: true,
      isHidden: true,
      ola: true,
      disabled: true,
      clickHere: true,
      currentNumber: 1,
      images: reactive([
        "stand-anim-left.gif",
        "stand-anim-right.gif",
        "walk-anim-left.gif",
        "walk-anim-right.gif",
      ]),
      timer: null,
      pageNumber: 1,
    };
  },
  mounted() {
    window.addEventListener("load", () => {
      if (this.$route.query.page === "/") {
        this.pageNumber = 1;
      }
      if (this.$route.query.page === "/About") {
        this.pageNumber = 2;
      }
      if (this.$route.query.page === "/Australia") {
        this.pageNumber = 3;
      }
      if (this.$route.query.page === "/Nz") {
        this.pageNumber = 4;
      }
    });
  },
  methods: {
    startThePage: function () {
      (this.isHidden = !this.isHidden), (this.ola = !this.ola);
    },
  },
  computed: {},
  watch: {
    $route(to, from) {
      this.clickHere = false;
      var fromPage = from.path;
      var toPage = to.path;
      var getNewArray = this.pageName.toString().split(/[\s,]+/);
      var x = getNewArray.indexOf(fromPage);
      var y = getNewArray.indexOf(toPage);
      var self = this;
      if (x < y) {
        this.pageNumber += 1;
        this.currentNumber = 3;
        self.delayshowLeft = false;
        self.delayshowRight = false;
        this.transitionName = "rightTrans";
        if (from.path != "/Nz") {
          setTimeout(function () {
            self.delayshowLeft = true;
            self.delayshowRight = true;
            self.currentNumber = 1;
          }, 5000);
        }
        if (to.path === "/Nz") {
          setTimeout(function () {
            self.delayshowRight = false;
            self.delayshowTrue = true;
            self.currentNumber = 1;
          }, 5000);
        }
      }
      if (x > y) {
        this.pageNumber -= 1;
        this.currentNumber = 2;
        this.transitionName = "leftTrans";
        self.delayshowLeft = false;
        self.delayshowRight = false;
        if (from.path != "/") {
          setTimeout(function () {
            self.delayshowLeft = true;
            self.delayshowRight = true;
            self.currentNumber = 1;
          }, 5000);
        }
        if (to.path === "/") {
          setTimeout(function () {
            self.delayshowRight = true;
            self.delayshowLeft = false;
            self.currentNumber = 1;
          }, 5000);
        }
      }
    },
  },
};
</script>
